.UserInfo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding: 50px;

  & > img {
    display: block;
    height: 180px;
    width: 120px;
    margin: 10px auto;
    border-radius: 35%;
    object-fit: cover;
  }

  :global {
    .info {
      color: rgb(24, 186, 138);
      margin: 10px auto;
      text-align: center;
    }

    .line {
      width: 300px;
      height: 2px;
      background-color: rgb(24, 186, 138);
      margin: 10px auto;
    }

    .H {
      width: 100%;
      color: rgb(24, 186, 138);
      text-align: center;
      font-size: 20px;
      margin: 10px auto;
    }

    .item {
      display: flex;
      color: rgb(24, 186, 138);
      line-height: 200px;
      white-space: nowrap;
      margin: 10px auto;
      justify-content: center;

      p {
        width: 500px;
        height: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      img {
        width: 300px;
        height: 200px;
        border-radius: 20px;
        margin: 10px;
      }
    }

    .phone {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      justify-content: center;

      img {
        height: 150px;
        width: 150px;
        object-fit: cover;
        margin: 0 10px;
      }
    }
  }
}
@primary-color: #3ace23;@layout-header-background: #24292f;